* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body,
html {
  background-color: rgb(248, 246, 246);
}

.left-pane {
  width: 17%;
  float: left;
  position: fixed;
  top: 0;
  height: 100vh;
  box-shadow: 8px 0 8px -8px rgba(0, 0, 0, 0.8);
}

.tablink {
  margin: 1rem;
  display: block;
}

.tablink > * {
  margin: 0 0.5rem 0 1rem;
  display: inline;
  align-self: center;
  justify-self: center;
  color: rgb(135, 207, 231);
}

.tablink a {
  font-size: 1rem;
  color: rgb(128, 125, 125);
  text-decoration: none;
}

.tablink a:hover {
  color: black;
}

.tablink a:active {
  color: black;
}

.sideheader {
  color: rgb(7, 169, 233);
  margin-bottom: 2rem;
  padding: 2rem 1rem;
  text-align: center;
  font-size: 1.5rem;
  background-color: #f7f7f7;
}

.right-pane {
  width: 83%;
  float: right;
  position: relative;
  background-color: #f7f7f7;
}

.grad-bg {
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 400px;
  background-image: linear-gradient(
    to right,
    rgb(164, 230, 252),
    rgb(7, 169, 233),
    rgb(22, 116, 240)
  );
}

.card {
  z-index: 2;
  display: inline-block;
  background-color: white;
  /* width: 20%; */
  padding: 1rem;
  margin: 1rem;
  border-radius: 5px;
  box-shadow: 0px 8px 8px -8px rgba(0, 0, 0, 0.8);
}

.card p:first-child {
  text-transform: uppercase;
  font-size: 13px;
}

.card p {
  text-transform: capitalize;
  margin: 0.3rem 0.7rem 0 0;
  color: rgb(82, 82, 82);
}

.card h2 {
  margin: 0.5rem 2rem 1.5rem 0;
  font-weight: 400;
}

.card-container {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.graph-container {
  z-index: 2;
  position: relative;
  width: 90%;
  height: 450px;
  background-color: rgb(17, 2, 56);
  border-radius: 5px;
  margin: 2rem auto;
  box-shadow: 0px 8px 8px -8px rgba(0, 0, 0, 0.8);
}

.table-container {
  width: 90%;
  margin: 3rem auto;
  border-radius: 5px;
}

.form-container {
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 2;
  position: relative;
  width: 90%;
  margin: auto;
  margin-top: 2rem;
  border-radius: 5px;
  box-shadow: 0 8px 8px -8px rgba(0, 0, 0, 0.8);
  padding: 1rem;
}

.form-container Button {
  /* width: 150px; */
  height: 45px;
  display: inline-block;
  margin: 1rem;
  border-radius: 30px;
  background-color: rgb(22, 116, 240);
  transition: all 0.5s ease-in-out;
}

.form-container Button:hover {
  background-color: rgb(7, 169, 233);
}

.form-container label {
  text-transform: capitalize;
  margin-right: 2rem;
}

.info-screen {
  padding: 1rem 3rem;
}

.info-screen li {
  padding: 0.5rem;
}

.info-screen h3 {
  margin-bottom: 1rem;
}
